$barami-color: rgb(88, 117, 65);

.barami-account {
  border: 2px solid $barami-color;
  border-radius: 10px;
  padding: 10px;
  .barami-account-title {
    font-weight: bold;
  }
  .barami-account-content {
    font-size: 90%;
  }
}


// 신현이 추가한 커스텀 패널들
// - 후원 계좌 및 방명록
.custom-panel {
  border: 2px solid $barami-color;
  border-radius: 10px;
  padding: 10px;
  margin: 10px 0;
  .custom-panel-title {
    font-weight: bold;
  }
  .custom-panel-content {
    font-size: 90%;
  }
  a {
    font-weight: bold;
    color: $barami-color;
  }
}